/* DCA specific styles */
.dca-container {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1rem;
}

.dca-results {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    width: 100%;
}

.dca-content {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 2rem;
    align-items: stretch;
    width: 100%;
}

/* Settings panel */
.dca-settings {
    display: flex;
    flex-direction: column;
    height: 100%;
    background: var(--color-surface);
    padding: 1.5rem;
    border-radius: var(--radius-lg);
    border: 1px solid var(--color-border);
    box-shadow: var(--shadow-sm);
    width: 100%;
    box-sizing: border-box;
}

.dca-title {
    margin: 0 0 2rem 0;
    font-size: 1.2rem;
    font-weight: 600;
    padding-bottom: 1rem;
    border-bottom: 1px solid var(--color-border);
}

.setting-group {
    margin-bottom: 1.5rem;
}

.setting-group:last-child {
    margin-bottom: 0;
}

.setting-group label {
    display: block;
    margin-bottom: 0.5rem;
    color: var(--color-text-secondary);
}

.input-group {
    display: flex;
    align-items: center;
    width: 100%;
}

.currency-symbol {
    padding: 0.5rem;
    border-right: none;
    border-radius: var(--radius-md) 0 0 var(--radius-md);
    background: var(--color-surface-hover);
    border: 1px solid var(--color-border);
    white-space: nowrap;
}

input[type="number"],
select {
    width: 100%;
    padding: 0.5rem;
    background: var(--color-surface-hover);
    border: 1px solid var(--color-border);
    border-radius: var(--radius-md);
    font-size: 1rem;
    color: var(--color-text);
    transition: all 0.2s ease;
    box-sizing: border-box;
}

input[type="number"]:focus,
select:focus {
    outline: none;
    border-color: var(--color-accent);
    box-shadow: 0 0 0 2px rgba(var(--color-accent-rgb), 0.2);
}

.input-group input[type="number"] {
    border-radius: 0 var(--radius-md) var(--radius-md) 0;
}

/* Results cards */
.result-card {
    text-align: center;
    background: var(--color-surface);
    padding: 1.5rem;
    border-radius: var(--radius-lg);
    border: 1px solid var(--color-border);
    box-shadow: var(--shadow-sm);
    width: 100%;
    box-sizing: border-box;
}

.result-label {
    color: var(--color-text-secondary);
    margin-bottom: 0.5rem;
}

.result-value {
    font-size: 1.5rem;
    font-weight: bold;
}

.result-value.positive {
    color: var(--color-accent-success);
}

.result-value.negative {
    color: var(--color-accent-danger);
}

/* Chart container */
.chart-container {
    height: 100%;
    background: var(--color-surface);
    padding: 1.5rem;
    border-radius: var(--radius-lg);
    border: 1px solid var(--color-border);
    box-shadow: var(--shadow-sm);
    width: 100%;
    box-sizing: border-box;
}

/* Coin selection styles */
.coin-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 1rem;
    margin-top: 1rem;
    width: 100%;
}

.coin-item {
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 1rem;
    cursor: pointer;
    transition: all 0.2s ease;
    background: var(--color-surface-hover);
    border: 1px solid var(--color-border);
    border-radius: var(--radius-md);
    width: 100%;
    box-sizing: border-box;
}

.coin-item:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-md);
    background: var(--color-hover);
}

.coin-info {
    flex: 1;
    min-width: 0; /* Prevents flex item from overflowing */
}

.coin-name {
    font-weight: 500;
    margin-bottom: 0.25rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.coin-symbol {
    font-size: 0.9rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.coin-action {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background: var(--color-accent-success);
    color: white;
    flex-shrink: 0;
}

.action-icon {
    font-size: 1rem;
    line-height: 1;
}

/* Responsive layout */
@media (max-width: 768px) {
    .dca-content {
        grid-template-columns: 1fr;
    }

    .dca-settings,
    .chart-container {
        height: auto;
        padding: 1rem;
    }

    .chart-container {
        min-height: 300px;
    }

    .dca-results {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .dca-container {
        padding: 0 0.5rem;
    }

    .setting-group {
        margin-bottom: 1rem;
    }
} 