.dca-container {
  flex-direction: column;
  gap: 2rem;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
}

.dca-results {
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  width: 100%;
  display: grid;
}

.dca-content {
  grid-template-columns: 1fr 2fr;
  align-items: stretch;
  gap: 2rem;
  width: 100%;
  display: grid;
}

.dca-settings {
  background: var(--color-surface);
  border-radius: var(--radius-lg);
  border: 1px solid var(--color-border);
  height: 100%;
  box-shadow: var(--shadow-sm);
  box-sizing: border-box;
  flex-direction: column;
  width: 100%;
  padding: 1.5rem;
  display: flex;
}

.dca-title {
  border-bottom: 1px solid var(--color-border);
  margin: 0 0 2rem;
  padding-bottom: 1rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.setting-group {
  margin-bottom: 1.5rem;
}

.setting-group:last-child {
  margin-bottom: 0;
}

.setting-group label {
  color: var(--color-text-secondary);
  margin-bottom: .5rem;
  display: block;
}

.input-group {
  align-items: center;
  width: 100%;
  display: flex;
}

.currency-symbol {
  border-radius: var(--radius-md) 0 0 var(--radius-md);
  background: var(--color-surface-hover);
  border-right: none;
  border: 1px solid var(--color-border);
  white-space: nowrap;
  padding: .5rem;
}

input[type="number"], select {
  background: var(--color-surface-hover);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  width: 100%;
  color: var(--color-text);
  box-sizing: border-box;
  padding: .5rem;
  font-size: 1rem;
  transition: all .2s;
}

input[type="number"]:focus, select:focus {
  border-color: var(--color-accent);
  box-shadow: 0 0 0 2px rgba(var(--color-accent-rgb), .2);
  outline: none;
}

.input-group input[type="number"] {
  border-radius: 0 var(--radius-md) var(--radius-md) 0;
}

.result-card {
  text-align: center;
  background: var(--color-surface);
  border-radius: var(--radius-lg);
  border: 1px solid var(--color-border);
  box-shadow: var(--shadow-sm);
  box-sizing: border-box;
  width: 100%;
  padding: 1.5rem;
}

.result-label {
  color: var(--color-text-secondary);
  margin-bottom: .5rem;
}

.result-value {
  font-size: 1.5rem;
  font-weight: bold;
}

.result-value.positive {
  color: var(--color-accent-success);
}

.result-value.negative {
  color: var(--color-accent-danger);
}

.chart-container {
  background: var(--color-surface);
  border-radius: var(--radius-lg);
  border: 1px solid var(--color-border);
  height: 100%;
  box-shadow: var(--shadow-sm);
  box-sizing: border-box;
  width: 100%;
  padding: 1.5rem;
}

.coin-grid {
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
  width: 100%;
  margin-top: 1rem;
  display: grid;
}

.coin-item {
  cursor: pointer;
  background: var(--color-surface-hover);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  box-sizing: border-box;
  align-items: center;
  gap: 1rem;
  width: 100%;
  padding: 1rem;
  transition: all .2s;
  display: flex;
}

.coin-item:hover {
  box-shadow: var(--shadow-md);
  background: var(--color-hover);
  transform: translateY(-2px);
}

.coin-info {
  flex: 1;
  min-width: 0;
}

.coin-name {
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-bottom: .25rem;
  font-weight: 500;
  overflow: hidden;
}

.coin-symbol {
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: .9rem;
  overflow: hidden;
}

.coin-action {
  background: var(--color-accent-success);
  color: #fff;
  border-radius: 50%;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  display: flex;
}

.action-icon {
  font-size: 1rem;
  line-height: 1;
}

@media (width <= 768px) {
  .dca-content {
    grid-template-columns: 1fr;
  }

  .dca-settings, .chart-container {
    height: auto;
    padding: 1rem;
  }

  .chart-container {
    min-height: 300px;
  }

  .dca-results {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .dca-container {
    padding: 0 .5rem;
  }

  .setting-group {
    margin-bottom: 1rem;
  }
}
